// mixin
import saleCommon from 'mixins/saleCommon'

const BASIC_INFO_KEY = 'HELP_BASIC_INFO';
const FORM_INFO_KEY = 'HELP_FORM_INFO';
export default {
  name: 'help-Report',
  mixins: [saleCommon],
  data() {
    return {
      areaList: [],
      bankList: [],
      areaValue: [],
      bankValue: [],
    };
  },
  methods: {
    //
    goCount() {
      if (!this.btnStatus) return;
      const ROSE_OPTION = JSON.parse(localStorage.getItem("ROSE_OPTION"));  //
      //
      let governor = this.governor,
        member = this.member,
        comments = this.comments;
      // 
      let tips = '';
      let tipArr = [
        {key: 'areaValue', msg: ''},
        {key: 'bankValue', msg: ''},
        // {key: 'helpCaptain', msg: ''},
        // {key: 'governor', msg: ''},
        // {key: 'member', msg: ''},
        {key: 'arr', msg: ''},
        {key: 'Aclass', msg: 'A'},
        {key: 'Bclass', msg: 'B'},
        {key: 'hotelAmt', msg: ''},
        {key: 'trafficAmt', msg: ''},
        {key: 'lTrafficAmt', msg: ''},
        {key: 'otherAmt', msg: ''},
      ];
      tipArr.every(item => {
        if (!this[item.key] && this[item.key] !== 0) {
          tips = `${item.msg}`;
          return;
        }
        else if (item.key === 'areaValue' && !this[item.key].length || this[item.key][0] === '' || item.key === 'bankValue' && !this[item.key].length) {
          tips = `${item.msg}`;
          return;
        }
        else if (item.key === 'arr') {
          if (!this.arr.length) {
            tips = `${item.msg}`;
            return false;
          }
          this[item.key].every(item => {
            if (item.value !== '' && !item.qty && item.qty !== 0) {
              tips = '';
              return false
            }
            return false
          });
          return !tips;
        }
        return true;
      })
      if (tips) {
        this.$vux.alert.show({
          content: tips
        });
        return;
      }
      // 
      let jsonData = {
        listId: "4bda3e47-a088-4749-a988-ebb07cfb00e4",
        referenceId: this.guid(),
        baseinfoExt: {
          id: this.guid(),
          varchar1: ROSE_OPTION.dept,         // () ()
          varchar2: this.monthCoverNum,             // 
          varchar3: this.areaValue[0],        // ()
          varchar4: this.bankValue[0],        // 
          varchar5: ROSE_OPTION.groupName,    // ()
          varchar6: '',                     // 
          varchar7: governor,                 //    
          varchar8: member,                   //    
          varchar9: comments,                 // 
          varchar10: ROSE_OPTION.region,      // ()
          varchar11: ROSE_OPTION.bank,        // ()
          varchar12: ROSE_OPTION.userCode,     // 
          varchar13: this.costVolumeRatio,   // 
          double7: this.hotelAmt, // 
          double8: this.otherAmt, // 
          double9: this.totalCost, // (+)
          double10: this.trafficAmt, // 
          double11: this.lTrafficAmt, // 
        },
        transDetailUncalc: [{
          id: this.guid(),
          transObjCode: "A", //
          containerCode: "A", //
          qty: "",
          amount: Number(this.Aclass), //
          fgCode: ""
        }, {
          id: this.guid(),
          transObjCode: "B", //
          containerCode: "B", //
          qty: "",
          amount: Number(this.Bclass), //
          fgCode: ""
        }
        ],
        transCode: "XHXSDD"
      };
      // 
      for (let i = 0; i < this.arr.length; i++) {
        let item = this.arr[i];
        jsonData.transDetailUncalc.push({
          id: this.guid(),
          transObjCode: item.value, //
          containerCode: "", //
          qty: item.qty,
          taxAmount: item.taxAmount,
          amount: item.qty * item.amount, //
          fgCode: "",
          num1: item.num1, // 
        });
      }
      let totalInfo = {
        isMobile: true,
        conn: 20000,
        list: "trans_form_data",
        transCode: "XHXSDD",
        jsonData: JSON.stringify(jsonData)
      };
      this.totalInfo = totalInfo;
      //  
      localStorage.setItem(
        "saleReportInfo",
        JSON.stringify({
          saleReportRemark: totalInfo,
          time: new Date().getTime()
        })
      );
      //  
      this.recordForm();
      //  
      this.recordBasic();

      this.$router.push({path: "/count"});
    },
    // TODO 
    recordBasic() {
      //  
      localStorage.setItem(
        [BASIC_INFO_KEY],
        JSON.stringify({
          // member: this.member,
          // governor: this.governor,
          // captain: this.helpCaptain,
          bank: this.bankValue[0],
          areaValue: this.areaValue[0],
        })
      );
    },
    // TODO 
    recordForm() {
      localStorage.setItem(
        [FORM_INFO_KEY],
        JSON.stringify({
          Aclass: this.Aclass,
          Bclass: this.Bclass,
          saleReportArr: this.arr,
          comments: this.comments,
          hotelAmt: this.hotelAmt,
          trafficAmt: this.trafficAmt,
          lTrafficAmt: this.lTrafficAmt,
          otherAmt: this.otherAmt,
          time: new Date().getTime()
        })
      );
    }
  },
  created() {
    let {monthCoverNum = ''} = this.$route.query;
    this.monthCoverNum = monthCoverNum;
    // 
    const basicInfo = JSON.parse(localStorage.getItem(BASIC_INFO_KEY)) || '';
    const formInfo = JSON.parse(localStorage.getItem(FORM_INFO_KEY)) || '';
    // 
    this.echoStorage(basicInfo, formInfo);
    // 
    this.getSuperior();
    // 
    this.getArea();
    // 
    this.getBank();
  },
  beforeRouteLeave(to, from, next) {
    if (!this.arr.length || to.name == "Count") next();
    else {
      this.$vux.confirm.show({
        title: "",
        content: "",
        confirmText: "",
        cancelText: "",
        onCancel: () => {
          localStorage.removeItem(BASIC_INFO_KEY);
          localStorage.removeItem(FORM_INFO_KEY);
          next();
        },
        onConfirm: () => {
          this.recordForm();
          //
          this.recordBasic();
          next();
        }
      });
    }
  }
};



